import React from "react";
import { BrowserRouter as Router, Route, Routes, Link } from "react-router-dom";
import "../App";

const App = () => {
  return (
    <Router>
      <div className="App">
        <NavBar />
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/features" element={<Features />} />
          <Route path="/pricing" element={<Pricing />} />
          <Route path="/resources" element={<Resources />} />
          <Route path="/faqs" element={<FAQs />} />
          <Route path="/contact" element={<Contact />} />
        </Routes>
        <Footer />
      </div>
    </Router>
  );
};

const NavBar = () => (
  <nav className="navbar">
    <Link to="/">Home</Link>
    <Link to="/features">Features</Link>
    <Link to="/pricing">Pricing</Link>
    <Link to="/resources">Resources</Link>
    <Link to="/faqs">FAQs</Link>
    <Link to="/contact">Contact</Link>
  </nav>
);

const Home = () => (
  <header className="hero">
    <h1>Transform Your Gym Experience</h1>
    <p>
      Streamline management, enhance member engagement, and boost your gym's
      performance.
    </p>
    <div className="cta-buttons">
      <a href="#get-started" className="btn">
        Get Started Free
      </a>
      <a href="#watch-demo" className="btn secondary">
        Watch Demo
      </a>
    </div>
  </header>
);

const Features = () => (
  <section id="features">
    <h2>Key Features</h2>
    <div className="feature-grid">
      {features.map((feature, index) => (
        <div className="feature" key={index}>
          <h3>{feature.title}</h3>
          <p>{feature.description}</p>
        </div>
      ))}
    </div>
  </section>
);

const Pricing = () => (
  <section id="pricing">
    <h2>Flexible Pricing Plans</h2>
    <div className="pricing-table">
      {pricingPlans.map((plan, index) => (
        <div className="pricing-plan" key={index}>
          <h3>{plan.name}</h3>
          <p>{plan.price}</p>
        </div>
      ))}
    </div>
    <a href="#choose-plan" className="btn">
      Choose Your Plan
    </a>
  </section>
);

const Resources = () => (
  <section id="resources">
    <h2>Resources for Gym Owners</h2>
    <ul>
      {resources.map((resource, index) => (
        <li key={index}>
          <a href={resource.link}>{resource.title}</a>
        </li>
      ))}
    </ul>
  </section>
);

const FAQs = () => (
  <section id="faqs">
    <h2>Frequently Asked Questions</h2>
    <ul>
      {faqs.map((faq, index) => (
        <li key={index}>
          <strong>{faq.question}</strong>
          <p>{faq.answer}</p>
        </li>
      ))}
    </ul>
  </section>
);

const Contact = () => (
  <section id="contact-banner" className="contact-banner">
    <h2>Need Custom Solutions?</h2>
    <p>
      We offer tailored solutions for gyms of all sizes. Get in touch with us
      to discuss your needs.
    </p>
    <a href="#contact-us" className="btn">
      Contact Us
    </a>
  </section>
);

const Footer = () => (
  <footer>
    <div className="footer-content">
      <p>&copy; 2025 Gym Management System. All rights reserved.</p>
      <div className="footer-links">
        <Link to="/faqs">FAQs</Link>
        <Link to="/contact">Contact</Link>
        <a href="#privacy-policy">Privacy Policy</a>
        <a href="#terms-of-service">Terms of Service</a>
      </div>
      <div className="social-media">
        <a href="#">Facebook</a>
        <a href="#">Twitter</a>
        <a href="#">Instagram</a>
        <a href="#">LinkedIn</a>
      </div>
    </div>
  </footer>
);

const features = [
  { title: "Member Management", description: "Easily manage memberships, track attendance, and engage with members." },
  { title: "Class Scheduling", description: "Create and manage classes effortlessly with our intuitive scheduling tool." },
  { title: "Payment Processing", description: "Secure and seamless payment processing for memberships and services." },
  { title: "Analytics Dashboard", description: "Gain insights into your gym's performance with real-time analytics." },
  { title: "Mobile App", description: "Empower your members with a mobile app for bookings and updates." },
];

const pricingPlans = [
  { name: "Basic Plan", price: "$29/month" },
  { name: "Pro Plan", price: "$59/month" },
  { name: "Enterprise Plan", price: "Custom pricing" },
];

const resources = [
  { title: "5 Tips to Increase Member Retention", link: "#" },
  { title: "How to Optimize Your Gym's Schedule", link: "#" },
  { title: "A Guide to Effective Gym Marketing", link: "#" },
  { title: "Choosing the Right Management Software", link: "#" },
];

const faqs = [
  { question: "Is the system suitable for small gyms?", answer: "Yes! Our plans are flexible and scalable to suit gyms of all sizes." },
  { question: "Do you offer a free trial?", answer: "Absolutely. We provide a 14-day free trial for all our plans." },
  { question: "Is the system mobile-friendly?", answer: "Yes, both the admin and member interfaces are fully optimized for mobile use." },
];

export default App;
